html,body{height:100%}body{background-color:#28dad4;text-align:center;justify-content:center;align-items:center}.stop-title{font-family:"Kaushan Script",cursive;font-size:10rem;padding:7% 0;color:white;text-shadow:6px 2px 0 #953DD9,10px 5px 0 coral,-15px 8px 10px rgba(0,0,0,0.4)}@media screen and (max-width: 600px){.stop-title{font-size:7rem;padding:20% 0}}.new-game{background-color:white;padding:10px 30px;border-radius:10px;font-family:"Neucha",cursive;font-size:18px;letter-spacing:2px}.card{margin:5vh auto;background-color:#fff;border:1px solid #e4e4e4;border-radius:0 0 125px 3px/3px 85px 5px 55px;padding:2rem 2rem 1.2rem;position:relative;min-height:200px;width:75%;background-image:linear-gradient(#e4e4e4 1px, transparent 1px),linear-gradient(90deg, #e4e4e4 1px, transparent 1px);background-size:20px 20px, 20px 20px, 20px 20px, 20px 20px;background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px}.card:before,.card:after{top:0;left:0.25%;height:100%;border:inherit;border-radius:4px;transform:rotate(-0.1deg) scaleX(-1);z-index:-1;width:99.5%}.card .header{width:90%;display:flex;justify-content:space-between;margin:0 50px}.card .header span{font-family:"Neucha",cursive;font-size:1.2rem;color:#953DD9;margin-bottom:10px;letter-spacing:1px;margin-right:20px}.card .hole{position:absolute;height:25px;width:25px;background:#28dad4;border-radius:50%;box-shadow:inset 0px 0px 2px 0px #888}@media screen and (max-width: 768px){.card{padding-bottom:3.2rem}}.card span{font-family:"Kalam",cursive;color:#953DD9;font-size:1.5rem}.card .hole-top{top:20%;left:30px}.card .hole-middle{top:45%;left:30px}.card .hole-bottom{bottom:20%;left:30px}.card .hole-top-right{top:20%;right:30px}.card .hole-middle-right{top:45%;right:30px}.card .hole-bottom-right{bottom:20%;right:30px}.table{display:grid;grid-template-columns:auto auto auto auto;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;grid-row-gap:10px;row-gap:10px;width:92%;margin-left:30px}@media screen and (max-width: 500px){.table{grid-template-columns:auto;margin-left:15%}}@media screen and (min-width: 600px) and (max-width: 940px){.table{grid-template-columns:auto auto}}.table label{font-family:"Neucha",cursive;font-weight:700;letter-spacing:1px;color:#1529be;text-transform:uppercase;letter-spacing:2px}.stop-button{min-width:180px;width:20%;transform:rotate(-2deg);font-family:"Kaushan Script",cursive;font-size:2.5rem;background-color:white;color:white;transition:0.25s;background-color:#00D800;border-radius:255px 15px 225px 15px/15px 225px 15px 255px}.stop-button:hover:enabled,.stop-button:focus:enabled,.stop-button:active{box-shadow:0 0 0 10em rgba(255,255,255,0);border-color:coral;color:white;background-color:coral;cursor:pointer}.stop-button:disabled,.stop-button:disabled:hover{background-color:#e4e4e4;color:white;border-color:#e4e4e4}@-webkit-keyframes pulse{0%{box-shadow:0 0 0 0 coral}}@keyframes pulse{0%{box-shadow:0 0 0 0 coral}}.stop-button.end{-webkit-animation:pulse 1s;animation:pulse 1s;box-shadow:0 0 0 10em rgba(255,255,255,0);border-color:coral;color:white;background-color:coral}.stop-button.end:disabled{color:white;border-color:coral;background-color:coral}.players-card{width:40%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;max-height:500px;position:absolute;top:0;left:0;bottom:0;right:0;margin:auto;padding:1rem;background:white;border:1px solid #ccc;border-radius:0 0 125px 3px/3px 85px 5px 55px;transition:1.1s ease-out;box-shadow:-0.5rem 0.5rem 1rem rgba(0,0,0,0.2);background-image:linear-gradient(#e4e4e4 1px, transparent 1px),linear-gradient(90deg, #e4e4e4 1px, transparent 1px);background-size:20px 20px, 20px 20px, 20px 20px, 20px 20px;background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px}@media screen and (max-width: 768px){.players-card{width:70%}}.players-card h1{font-family:"Neucha",cursive;color:#1529be}.players-card ul{list-style-type:none;width:50%;margin:0 auto;font-size:1.5rem;font-family:"Kalam",cursive;color:rgba(0,0,0,0.5)}.players-card ul li{background-image:linear-gradient(to right, transparent 50%, rgba(40,218,212,0.4) 50%);background-position:-0% 0;background-size:200% auto;transition:all 0.8s ease-out;transform:rotate(-4deg)}.players-card ul li:hover{background-position:-99.9% 0;cursor:pointer}.players-card ul li.selected::after{content:"âœ“";margin-left:20px;color:coral;font-weight:700}.players-card span{font-family:"Neucha",cursive}.players-card input{margin:10% 0 5% 0}.purple-button{background-color:rgba(149,61,217,0.3);border-radius:0 0 125px 3px/3px 85px 5px 55px;font-size:1.2rem;font-family:"Neucha",cursive;border:none;transition:all 0.4s ease-in-out}.purple-button:hover{background-color:rgba(149,61,217,0.5);cursor:pointer}.flip-card{background-color:transparent;perspective:3000px;min-height:260px}.flip-card-inner{position:relative;width:100%;height:100%;text-align:center;transition:transform 1s;transform-style:preserve-3d}.flip-card.flip .flip-card-inner{transform:rotateY(180deg)}.flip-card-front,.flip-card-back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.flip-card-back{transform:rotateY(180deg)}input{border-radius:255px 15px 225px 15px/15px 225px 15px 255px;padding:10px;border:1px solid #bebebe;background-color:transparent;font-family:"Kalam",cursive;font-size:18px;text-align:center;letter-spacing:1px;width:90%;color:#656565}input:focus{outline:none;border:1px solid #953DD9}.start-game label{font-family:"Neucha",cursive;font-size:1.5rem;color:#1529be}.start-game input{width:5%;border-radius:255px 15px 225px 15px/15px 225px 15px 255px;padding:10px;margin-bottom:2rem;border:1px solid #bebebe;background-image:linear-gradient(#e4e4e4 1px, transparent 1px),linear-gradient(90deg, #e4e4e4 1px, transparent 1px);background-size:20px 20px, 20px 20px, 20px 20px, 20px 20px;background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px;background-color:white;font-family:"Kalam",cursive;font-size:18px;text-align:center;color:#656565}.start-game input:focus{outline:none;border:1px solid #953DD9}#snackbar{visibility:hidden;min-width:250px;margin-left:-140px;background-color:#601d93;color:#fff;text-align:center;border-radius:8px;padding:16px;position:fixed;z-index:1;left:50%;top:150px;font-size:30px;font-family:"Neucha",cursive;box-shadow:-5px 5px 15px gray}#snackbar.show{visibility:visible;-webkit-animation:fadein 0.5s, fadeout 0.5s 2.5s;animation:fadein 0.5s, fadeout 0.5s 2.5s}@-webkit-keyframes fadein{from{top:0;opacity:0}to{top:150px;opacity:1}}@keyframes fadein{from{top:0;opacity:0}to{top:150px;opacity:1}}@-webkit-keyframes fadeout{from{top:150px;opacity:1}to{top:0;opacity:0}}@keyframes fadeout{from{top:150px;opacity:1}to{top:0;opacity:0}}.players-list h2{font-family:"Neucha",cursive;color:#1529be}.players-list ol{width:80%;margin:0 auto;overflow:auto;text-align:left}.players-list ol li{float:left;width:33%;font-family:"Kalam",cursive;color:#953DD9;font-size:22px}@media screen and (max-width: 600px){.players-list ol li{width:100%}}@media screen and (min-width: 600px) and (max-width: 940px){.players-list ol{width:90%}.players-list ol li{width:50%}}.menu button{background-color:transparent;border:none;margin:0 20px}.menu button:hover:enabled{cursor:pointer}.menu button:focus{outline:none}.menu{width:90%;justify-content:space-between;display:inline-flex;text-align:center}@media screen and (max-width: 500px){.menu{width:100%}}.menu__link{display:inline-block;color:#989898;text-decoration:none;position:relative;padding:14px 0;font-family:"Kalam",cursive;font-weight:300;font-size:16px;line-height:1;letter-spacing:0.040em}.menu__link svg{fill:none;stroke:#7e7e7e;stroke-width:2;stroke-miterlimit:10;stroke-dasharray:338;stroke-dashoffset:338;stroke-linecap:round;position:absolute;top:45%;left:50%;width:calc(100% + 45px);opacity:0;transform:translate(-50%, -50%);transition:stroke-dashoffset 0s 0.2s, opacity 0.2s}.menu__link__active svg{stroke:#ff4c4c}.menu__link__active svg,.menu__link:hover:enabled svg{stroke-dashoffset:0;opacity:1;transition:opacity 0s,stroke-dashoffset 0.3s cubic-bezier(0.645, 0.045, 0.355, 1)}

